
	import logo from '../../assets/logo.png'
	import XHeader from 'vux/src/components/x-header/index.vue'

	export default{
		components : {
			XHeader
		},
		data() {
			return {
     			menu : false,
     			logo : logo,
     			nav : [
     				{
     					title : '首页',
     					href : '/'
     				},
     				{
     					title : '关于我们',
     					href : '/about'
     				},
     				{
     					title : '服务介绍',
     					href : '/service'
     				},
     				{
     					title : '新闻资讯',
     					href : '/newsList'
     				},
     				{
     					title : '联系我们',
     					href : '/contact'
     				}
     			]
     		}
		},
		methods : { 
			menus (){
			  this.menu = !this.menu
		 	},
		 	headshake (ev){	
			
				ev.target.classList.add('active');
				
				setTimeout(function (){	
					ev.target.classList.remove('active');
				},750);
			
			}
		}
	}
	



